function sketch (p){

    //let rotation = 0;
    var img;
    var angle = 0;
    var r = 200;
    //let data;

    p.preload= function () {
  //data = loadTable(url, 'csv', 'header');
p.table = p.loadTable('total_imports_sums_final.csv', 'csv', 'header');
//p.table_2 = p.loadTable('all_exports_final.csv', 'csv', 'header');
   img = p.loadImage('world.jpeg')



}
p.display_import= function(){


  for (let row of p.table.rows) {
    let lat = row.getNum('Latitude');
    let lon = row.getNum('Longitude');
    let mag = row.getNum('Trade Value (US$)');

     let theta = p.radians(lat);

    let phi = p.radians(lon) + p.PI;

       let x = r * p.cos(theta) * p.cos(phi);
    let y = -r * p.sin(theta);
    let z = -r * p.cos(theta) * p.sin(phi);

    let pos = p.createVector(x, y, z);

    let h = p.pow(10, mag);
    let maxh = p.pow(10, 7);
    h = p.map(h, 0, maxh, 10, 100);
    let xaxis = p.createVector(1, 0, 0);

    let angleb = p.abs(xaxis.angleBetween(pos));

    let raxis = xaxis.cross(pos);

    p.push();
    p.translate(x, y, z);
    // In p5.js, the rotation axis is a vector object instead of x,y,z
    p.rotate(angleb, raxis);
    p.fill('blue');
    p.box(100, 5, 5);
    p.pop();


}

}



    p.setup = function (){
        p.createCanvas(1500,500);
    };

    p.draw= function () {
        //if (p.mouseIsPressed) {
          //  p.fill(0);
       // } else {
            //p.fill(255);
       // }
        //p.ellipse(p.mouseX, p.mouseY, 80, 80);

        p.background(0);
   p.rotateY(angle);
  angle += 0.01;
  p.noStroke();
  p.texture(img);
  p.sphere(r);
  p.display_import();

    };
};
    export default sketch;